@import "reset";
@import "base";
@import "css3";
@import "header_bar";

.upload-area {
    margin-top: 35px;
    & > h2 {
        font-size: 20px;
        margin-bottom: 15px;
        font-weight: bold;
        padding-left: 10px;
    }
}

.upload-container {
    background-color: #fff;
    padding: 35px 0;
}

.upload-progress-bar {
    width: 80%;
    margin: 0 auto;
    font-size: 0;
    text-align: center;
    & > li {
        display: inline-block;
    }
}

.progress-point {
    position: relative;
    width: 63px;
    height: 63px;
    vertical-align: middle;
    img {
        position: relative;
        width: 100%;
        z-index: 2;
    }
    span {
        position: absolute;
        top: 45%;
        left: 50%;
        width: 20px;
        height: 20px;
        background-color: #282828;
        border-radius: 50%;
        @include translate(-50%, -50%);
    }
}

.progress-join-line {
    width: 35%;
    height: 2px;
    margin: 0 10px;
    background-color: #282828;
}

.add-file-container {
    width: 60%;
    margin: 70px auto 0;
    border: 2px dashed #bebebe;
    background-color: #f2f2f2;
    padding: 40px 0;
    p {
        color: #8c8c8c;
        line-height: 1.5;
        font-size: 14px;
        text-align: center;
    }
}

.upload-file-component {
    text-align: center;
    margin-bottom: 28px;
    label {
        display: inline-block;
        padding: 15px 40px;
        background-color: #ee344a;
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
    }
    input[type="file"] {
        display: none;
    }
}

.upload-file-form {
    width: 60%;
    margin: 0 auto;
    padding-top: 50px;
}

.file-item {
    font-size: 0;
    margin-bottom: 20px;
    text-align: center;
    h3,
    input,
    textarea {
        display: inline-block;
        vertical-align: top;
        font-size: 15px;
    }
    h3 {
        width: 12%;
        margin-right: 25px;
        text-align: right;
    }
    input,
    textarea {
        width: 62%;
        border: 1px solid #ccc;
        @include box-sizing(border-box);
        padding: 5px 10px;
    }
    textarea {
        resize: none;
        height: 90px;
    }
}

.upload-file-button {
    padding: 15px 35px;
    background-color: #ee344a;
    color: #fff;
    font-size: 20px;
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}